body{
  background-color: #d4d4d4;
}

.time-display {
  font-size: 16px; /* ขนาดของตัวอักษร */
  font-weight: bold;
  color: #333; /* สีของข้อความ */
  margin-left: -750px; /* ระยะห่างจากด้านซ้าย */
  text-align: left; /* ทำให้เวลาอยู่ทางขวา */
  margin-top: 20px; /* ระยะห่างจากด้านบน */
}

.navbar {
  border-bottom: 2px solid #d4d4d4;
  background-color: #d4d4d4;
  padding: 20px 0;
  border-bottom: 1px solid black;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  max-width: 1200px;
  margin: 0 auto;
  height: 30px;
}

.logo {
  flex: 1; 
}

.logo img {
  margin-top: 10px;
  height: 100px;
  margin-left: -150px;
}

select {
  background-color: #d4d4d4;
  padding: 8px 12px;
  cursor: pointer; 
  font-family: 'JasmineUPC', sans-serif;
  border: none;
  font-size: 27px; 
  align-items: center;
  font-weight: bold;
}

select:hover {
  background-color: #b8b7b7d7;
  border-radius: 5px;
} 
  
select option {
  background-color: #d4d4d4;
  color: black;
  padding: 5px;
  font-size: 20px;
  font-family: 'JasmineUPC', sans-serif;
  border-radius: 5px;
}

option:hover {
  background-color: #b8b7b7d7;
  border-radius: 5px;
} 
  
.mapImage img {
  width: 100%;
  max-width: 1920px;  
  margin: 0 auto; 
  height: auto;
}

.nav-links{
  list-style: none;
  display: flex;
  gap: 50px;
  padding-left: 10px;
  padding-right: 10px;
  font-family: 'JasmineUPC', sans-serif;
  font-size: 27px; 
  align-items: center;
  font-weight: bold;
  margin-right: -100px;
}

.no-underline {
  text-decoration: none;
  color: black;
}
  

  